<template>
    <div class="container post">
        <v-alert type= "info" dismissible light>
          Thoughts 101 is a free platform where you can share your Thoughts to the world
        </v-alert>
        <h1>Latest Thoughts</h1>
         <v-alert type="warning" v-if="error" dismissible>
     {{error}}
    </v-alert>
    <div 
      v-for="(post, index) in posts"
      v-bind:item="post"
      v-bind:index="index"
      v-bind:key="post._id"
      v-on:dblclick="deletePost(post._id)"
    >
       <v-container class="grey lighten-5">
    <v-row
      no-gutters
    >
      <v-col>
        <v-card
          max-height='500000px'
          hover
          tile
          shaped
          class="card"
        >
           <h3 >{{post.title}}</h3>
           <p class="body">{{post.body}}</p>
           <p class="name">Written by {{post.name}}</p>
        </v-card>
        <br>
      </v-col>
    </v-row>
  </v-container>
    </div>
    <div class="footer">
      <Footer/>
    </div>
        
    </div>
</template>

<script>
import DataService from '../PostService';
import Footer from '../components/layout/Footer';
export default {
    name:"PostComponent",
    data() {
        return {
            error :'',
            posts:[]
        }
    },

   async created() {
      try {
          const data = await DataService.getPosts();
          this.posts = data;
      } catch (err) {
          this.error = err
      }  
    },
    components:{
        Footer
    },
    methods: {
        async deletePost(id){
            try {
                await DataService.deletePost(id);
                this.posts = await DataService.getPosts();
            } catch (err) {
                console.log(err)
            }
        }
    },
}
</script>

<style scoped>
body {
    position: relative;
}
h3{
    text-align: center;
   padding: 10px 0;
    font-size: 23px;
}

.card {
    position: relative;
    z-index: 1;
}

.card .body {
 padding-bottom: 30px;
  margin-left: 20px;
  font-size: 20px;
}

.card .name {
   position: absolute;
   left: 50%;
   bottom: 0;
   transform: translateX(-50%);
   font-size: 12px;
   margin-top: 5px;

}

</style>